.portfolio {
    background: linear-gradient(var(--bg-color), var(--second-bg-color), var(--second-bg-color), var(--second-bg-color), var(--second-bg-color), var(--second-bg-color));
}

.portfolio h2 {
    margin-bottom: 4rem;
}

.protfolio-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    align-items: center;
    gap: 1rem;
}

.portfolio-box {
    position: relative;
    border-radius: 2rem;
    box-shadow: 0 0 1rem var(--bg-color);
    overflow: hidden;
    display: flex;
    height: 99%;
    width: 99%;
}

.portfolio-box img {
    width: 100%;
    transition: .5s ease;
}

.portfolio-box:hover img {
    transform: scale(1.1);
}

.portfolio-layer {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(var(--bg-half-opacity), var(--main-half-opacity));
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
    text-align: center;
    padding: 0 4rem;
    transform: translateY(100%);
    transition: .5s ease;
}

.portfolio-box:hover .portfolio-layer {
    transform: translateY(0);
}

.portfolio-layer h4 {
    font-size: 2.8rem;
}

.portfolio-layer p {
    font-size: 1.4rem;
    margin: .3rem 0 1rem;
}

.portfolio-links-container {
    display: flex;
    flex-direction: row;
}

.portfolio-layer div a {
    display: flex;
    align-items: center;
    font-weight: bold;
    font-size: 1.2rem;
    background: var(--bg-color);
    border-radius: 5rem;
    padding: .9rem;
    margin: 1rem;
    color: var(--text-color);
}

.portfolio-layer div a i {
    font-size: 1.8rem;
    margin-right: .4rem;
}

